<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name：</h3>
                <div class="text">Picker</div>
            </li>
            <li>
                <h3>Introduction：</h3>
                <div class="text">移动端的联动选择器插件</div>
            </li>
            <li>
                <h3>Demo（选择器）：</h3>
                <p class="btn" @click.stop="picker1()">demoData1</p>
                <p class="btn" @click.stop="picker2()">demoData2</p>
                <p class="btn" @click.stop="picker3()">demoData3</p>
                <div class="text">
                    您选择了：<input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Function：</h3>
<pre class="JS">
// 例：城市数据
let cityData = {
    "Code": "310000",
    "Name": "上海市",
    "level": [
        {
            "Code": "310000",
            "Name": "上海市",
            "sort": 1,
            "level": [
                {
                    "Code": "312000",
                    "Name": "黄浦区",
                    "sort": 1
                }
            ]
        }
    ]
}
</pre>

<pre class="JS">
// 选择器
new Picker({
    //标题(可选)
    "title": '请选择',

    //默认值（可选）
    "defaultValue": self.nowVal,

    //需要联动级数[1、2、3]（可选）
    //不传时默认获取数据的深度,最多3级
    "type": 1,

    //数据(必传)
    "data": cityData,

    //数组内的键名称[id、text、data](必传)
    "keys": {
        "id": "Code",
        "value": "Name",
        "childData": "level"
    },

    "callBack": function (val) {
        //回调函数（val为选择的值）
        self.nowVal = val;
    }
});
</pre>

            </li>
            <li>
                <h3>Usage：</h3>
                <div class="text">
                    引入文件：</br>
                    'picker/picker.css'</br>
                    'picker/picker.min.js'
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import Picker from '../../lib/picker/picker.min'
    import cityData from '../../lib/picker/city.js'

	export default {
		name: 'picker',
        data(){
            return {
                nowVal: ''
            }
        },
        methods: {
            // 选择器
            picker1: function () {
                var self = this;
                new Picker({
                    "title": '请选择',//标题(可选)
                    "defaultValue": self.nowVal,//默认值（可选）
                    "type": 1,//需要联动级数[1、2、3]（可选、不传时默认获取数据的深度,最多3级）
                    "data": cityData,//数据(必传)
                    "keys": {
                        "id": "Code",
                        "value": "Name",
                        "childData": "level"//最多3级联动
                    },//数组内的键名称(必传，id、text、data)
                    "callBack": function (val) {
                        //回调函数（val为选择的值）
                        self.nowVal = val;
                    }
                });
            },

            picker2: function () {
                var self = this;
                new Picker({
                    "title": '请选择',//标题(可选)
                    "defaultValue": self.nowVal,//默认值-多个以空格分开（可选）
                    "type": 2,//需要联动级数[1、2、3]（可选）
                    "data": cityData,//数据(必传)
                    "keys": {
                        "id": "Code",
                        "value": "Name",
                        "childData": "level"//最多3级联动
                    },//数组内的键名称(必传，id、text、data)
                    "callBack": function (val) {
                        //回调函数（val为选择的值）
                        self.nowVal = val;
                    }
                });
            },

            picker3: function () {
                var self = this;
                new Picker({
                    "title": '请选择',//标题(可选)
                    "defaultValue": self.nowVal,//默认值-多个以空格分开（可选）
                    "data": cityData,//数据(必传)
                    "keys": {
                        "id": "Code",
                        "value": "Name",
                        "childData": "level"//最多3级联动
                    },//数组内的键名称(必传，id、text、data)
                    "callBack": function (val) {
                        //回调函数（val为选择的值）
                        self.nowVal = val;
                    }
                });
            }
        }
	}
</script>

<style scoped>
    /*app.vue 模块中已引入*/
    /*@import "../../lib/datePicker/picker.css";*/
</style>


